.radio__label {
  position: relative;
  padding-left: 70px;
  display: inline-block;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.67;
  color: #283645;
}

.radio {
  display: block;
  margin-bottom: 46px;
}

.radio__input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.radio__label::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 35px;
  height: 35px;
  border: 2px solid #e9e9e9;
  border-radius: 50%;
  background-color: #ffffff;
}

.radio__label::before {
  content: "";
  position: absolute;
  z-index: 1;
  left: 12px;
  top: 7px;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background: @color-danger;
  display: none;
}

input:checked + span::before {
  display: block;
}

@media (min-width: @tablet-width) {
  .radio__input {
    margin-left: 59px;
  }

  .radio__label {
    margin-left: 58px;
    padding-left: 59px;
  }
}

@media (min-width: @desktop-width) {
  .radio__label {
    margin-left: 75px;
  }

  .radio__label::before {
    top: 8px;
  }
}
