.container {
  box-sizing: border-box;
  width: 320px;
  margin: 0 auto;
  padding-right: 20px;
  padding-left: 20px;
}

.container--mobile {
  width: 100%;
}

@media (min-width: @tablet-width) {
  .container {
    box-sizing: border-box;
    width: @tablet-width;
    margin: 0 auto;
    padding-right: 20px;
    padding-left: 20px;
  }

  .container--mobile {
    width: 100%;
  }
}

@media (min-width: @desktop-width) {
  .container {
    box-sizing: border-box;
    width: @desktop-width;
    margin: 0 auto;
    padding-right: 10px;
    padding-left: 10px;
  }

  .container--desktop {
    width: auto;
    margin: 0;
    padding: 0;
  }
}
