.toggle {
  width: 25px;
  height: 25px;
  padding: 0;
  font-size: 0;
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  background-repeat: no-repeat;
}

.toggle--closed {
  background-image: url(../img/icon-menu-burger.svg);
  width: 40px;
}

.toggle--opened {
  background-image: url("../img/icon-menu-cross.svg");
}

@media (min-width: @desktop-width) {
  .toggle {
    display: none;
  }
}
